#xerodesk-wrapper {
  #xerodesk-content {
    background: #ffffff;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
    position: fixed;
    border-radius: 5px;
    right: 20px;
    bottom: 100px;
    height: calc(100% - 130px);
    width: 380px;
    overflow: hidden;
    z-index: 99999;
    visibility: hidden;
    opacity: 0;
    transform: translateY(25px);
    transition: all 250ms ease-in-out;

    iframe {
      position: fixed;
    }
  }
  #xerodesk-toggler {
    background: #f68a1f;
    border-radius: 10px 40px 40px 40px;
    box-sizing: border-box;
    right: 20px;
    bottom: 20px;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 60px;
    padding: 0.75rem;
    z-index: 2;
    transition: all 250ms ease-in-out;

    img {
      width: 30px;
    }
  }
  &.open {
    #xerodesk-content {
      visibility: visible;
      opacity: 1;
      transform: translateY(0px);
    }
    #xerodesk-toggler {
      border-radius: 40px 10px 40px 40px;
      top: auto;
      z-index: 100000;

      img {
        width: 20px;
      }
    }
  }
}

